<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo">Vue</h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay = true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="updataCaptcha"></a>
        </div>
      </div>
      <div class="login_content">
        <form @submit.prevent="login">
          <!--  -->
          <div :class="{on: loginWay}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button
                :disabled="!phonePass"
                class="get_verification"
                :class="{phonePass_class: phonePass}"
                @click.prevent="sendCode"
                v-text="time ? `(${time}s)` : ''"
              ></button>
            </section>
            <section class="login_verification">
              <input type="number" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <!--  -->
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="//" v-model="name">
              </section>
              <section class="login_verification">
                <input
                  :type="typeChange ? 'password' : 'text'"
                  maxlength="8"
                  placeholder=""
                  v-model="pwd"
                >
                <div
                  class="switch_button"
                  @click="typeChange = !typeChange"
                  :class="{on: !typeChange, off: typeChange}"
                >
                  <div class="switch_circle" :class="{right: !typeChange}"></div>
                  <span class="switch_text" v-text="typeChange ? '...' : 'abc'"></span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img
                  class="get_verification"
                  src="http://localhost:4001/captcha"
                  alt="captcha"
                  @click="changeCaptcha"
                  ref="captcha"
                >
              </section>
            </section>
          </div>
          <button class="login_submit"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </a>
    </div>
    <LoginAlert :alertText="alertText" v-if="alertShow" @closeTip="closeTip"/>
  </section>
</template>

<script>
import LoginAlert from "../../components/LoginAlert/LoginAlert";
import {
  sendYanZhengMa,
  duanXinLogin,
  passwordLogin
} from "../../api/index.js";
export default {
  data() {
    return {
      loginWay: true, //
      time: 0, //
      typeChange: true, //

      //*
      phone: "", //
      code: "", //

      //*
      name: "", //
      pwd: "", //
      captcha: "", //
      alertText: "", //
      alertShow: false //
    };
  },
  components: {
    LoginAlert
  },
  computed: {
    phonePass() {
      return /^1\d{10}$/.test(this.phone);
    }
  },
  methods: {
    //*
    updataCaptcha() {
      this.loginWay = false;
      this.changeCaptcha();
    },
    //*
    async sendCode() {
      //*
      if (!this.time) {
        this.time = 60;
        this.timeId = setInterval(() => {
          this.time -= 1;
          if (this.time <= 0) {
            clearInterval(this.timeId);
          }
        }, 1000);
      }
      //*
      const result = await sendYanZhengMa(this.phone);
      if (result.code === 0) {
        this.showAlert("");
      } else if (result.code === 1) {
        this.showAlert(result.msg);
        if (this.time) {
          //
          clearInterval(this.timeId);
          this.time = 0;
        }
      }
    },
    showAlert(alertText) {
      this.alertShow = true;
      this.alertText = alertText;
    },
    //*
    async login() {
      // const { phonePass } = this;
      let result;
      //*
      if (this.loginWay) {
        //
        if (!this.phonePass) {
          //
          this.showAlert("");
          return;
        } else if (!/^\d{6}$/.test(this.code)) {
          //
          this.showAlert("6");
          return;
        }
        //*ajax
        result = await duanXinLogin({ phone: this.phone, code: this.code });
      } else {
        //
        if (!/^[a-zA-z]\w{3,15}$/.test(this.name)) {
          //4-16
          this.showAlert("4-16");
          this.changeCaptcha();
          return;
        } else if (!this.pwd) {
          //
          this.showAlert("");
          this.changeCaptcha();
          return;
        } else if (!this.captcha) {
          //
          this.showAlert("");
          this.changeCaptcha();
          return;
        }
        //*ajax
        result = await passwordLogin({
          name: this.name,
          pwd: this.pwd,
          captcha: this.captcha
        });
      }
      if (result.code === 0) {
        // 
        const user = result.data;
        //actions 
        this.$store.dispatch("getUserMsg", user);
        // 
        this.$router.replace("/profile");
      } else {
        const msg = result.msg;
        //
        this.showAlert(msg);
        //
        this.changeCaptcha();
      }
    },
    // 
    closeTip() {
      this.alertShow = false;
      this.alertText = "";
      if (this.loginWay) {
        //
        this.phone = "";
        this.code = "";
      } else {
        this.name = "";
        this.pwd = "";
        this.captcha = "";
      }
    },
    //*
    changeCaptcha() {
      this.$refs.captcha.src = `http://localhost:4001/captcha?time=${Date.now()}`;
    }
  }
};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import '../../common/stylus/mixins.styl';

.loginContainer {
  width: 100%;
  height: 100%;
  background: #fff;

  .loginInner {
    padding-top: 60px;
    width: 80%;
    margin: 0 auto;

    .login_header {
      .login_logo {
        font-size: 40px;
        font-weight: bold;
        color: #02a774;
        text-align: center;
      }

      .login_header_title {
        padding-top: 40px;
        text-align: center;

        >a {
          color: #333;
          font-size: 14px;
          padding-bottom: 4px;

          &:first-child {
            margin-right: 40px;
          }

          &.on {
            color: #02a774;
            font-weight: 700;
            border-bottom: 2px solid #02a774;
          }
        }
      }
    }

    .login_content {
      >form {
        >div {
          display: none;

          &.on {
            display: block;
          }

          input {
            width: 100%;
            height: 100%;
            padding-left: 10px;
            box-sizing: border-box;
            border: 1px solid #ddd;
            border-radius: 4px;
            outline: 0;
            font: 400 14px Arial;

            &:focus {
              border: 1px solid #02a774;
            }
          }

          .login_message {
            position: relative;
            margin-top: 16px;
            height: 48px;
            font-size: 14px;
            background: #fff;

            .get_verification {
              height: 36px;
              position: absolute;
              top: 50%;
              right: 10px;
              transform: translateY(-50%);
              border: 0;
              color: #ccc;
              font-size: 14px;
              background: transparent;

              &.phonePass_class {
                border-radius: 10px;
                background-color: #4cd96f;
                color: black;
              }
            }
          }

          .login_verification {
            position: relative;
            margin-top: 16px;
            height: 48px;
            font-size: 14px;
            background: #fff;

            .switch_button {
              font-size: 12px;
              border: 1px solid #ddd;
              border-radius: 8px;
              transition: background-color 0.3s, border-color 0.3s;
              padding: 0 6px;
              width: 30px;
              height: 16px;
              line-height: 16px;
              color: #fff;
              position: absolute;
              top: 50%;
              right: 10px;
              transform: translateY(-50%);

              &.off {
                background: #fff;

                .switch_text {
                  float: right;
                  color: #ddd;
                }
              }

              &.on {
                background: #02a774;
              }

              >.switch_circle {
                position: absolute;
                top: -1px;
                left: -1px;
                width: 16px;
                height: 16px;
                border: 1px solid #ddd;
                border-radius: 50%;
                background: #fff;
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
                transition: transform 0.3s;

                &.right {
                  transform: translateX(27px);
                }
              }
            }
          }

          .login_hint {
            margin-top: 12px;
            color: #999;
            font-size: 14px;
            line-height: 20px;

            >a {
              color: #02a774;
            }
          }
        }

        .login_submit {
          display: block;
          width: 100%;
          height: 42px;
          margin-top: 30px;
          border-radius: 4px;
          background: #4cd96f;
          color: #fff;
          text-align: center;
          font-size: 16px;
          line-height: 42px;
          border: 0;
        }
      }

      .about_us {
        display: block;
        font-size: 12px;
        margin-top: 20px;
        text-align: center;
        color: #999;
      }
    }

    .go_back {
      position: absolute;
      top: 5px;
      left: 5px;
      width: 30px;
      height: 30px;

      >.iconfont {
        font-size: 20px;
        color: #999;
      }
    }
  }
}
</style>



