/*
 *state
 */
export default {
  //
  totalCount(state) {
    //
    return state.foodsChoosed.reduce((total, food) => {
      return (total = total + food.count);
    }, 0);
  },
  totalPrice(state) {
    //
    return state.foodsChoosed.reduce((total, food) => {
      return (total = total + food.count * food.price);
    }, 0);
  },
  //
  positiveSize(state) {
    return state.shopPingJia.reduce((total, pingjia) => {
      return (total = total + (pingjia.rateType === 0 ? 1 : 0));
    }, 0);
  }
};



