/*
 * actions
 */

import Vue from "vue";

import {
  RECEIVE_FOOD_LIST,
  RECEIVE_SHOP_LIST,
  RECEIVE_ADDRESS,
  RECEIVE_USER_MSG,
  LOGIN_OUT,
  RECEIVE_SHOP_GOODS,
  RECEIVE_SHOP_PINGJIA,
  RECEIVE_SHOP_MSG,
  JIA_COUNT,
  JIAN_COUNT,
  CLEAR_CART,
  SEARCH_SHOP_LIST
} from "./mutation-types";

export default {
  //*
  [RECEIVE_FOOD_LIST](state, { foodList }) {
    state.foodList = foodList;
  },
  //*
  [RECEIVE_SHOP_LIST](state, { shopList }) {
    state.shopList = shopList;
  },
  //*
  [RECEIVE_ADDRESS](state, { address, latitude, longitude }) {
    state.address = address;
    state.latitude = latitude;
    state.longitude = longitude;
  },
  //*
  [RECEIVE_USER_MSG](state, { userMsg }) {
    state.userMsg = userMsg;
  },
  //*
  [LOGIN_OUT](state) {
    state.userMsg = {};
  },
  //*mock

  //
  [RECEIVE_SHOP_GOODS](state, { shopGoods }) {
    state.shopGoods = shopGoods;
  },
  //
  [RECEIVE_SHOP_PINGJIA](state, { shopPingJia }) {
    state.shopPingJia = shopPingJia;
  },
  //
  [RECEIVE_SHOP_MSG](state, { shopMsg }) {
    state.shopMsg = shopMsg;
  },
  [JIA_COUNT](state, { food }) {
    if (!food.count) {
      /*
       *food.count = 1; 
       *Vue.set(, "", )
       */
      Vue.set(food, "count", 1);
      //*foodcountfoodfoodsChoosed
      state.foodsChoosed.push(food);
    } else {
      food.count++;
    }
  },
  [JIAN_COUNT](state, { food }) {
    if (food.count) {
      food.count--;
      //*foodsChoosedfoodcount0food
      if (food.count === 0) {
        state.foodsChoosed.splice(state.foodsChoosed.indexOf(food), 1);
      }
    }
  },
  //*
  [CLEAR_CART](state) {
    //foodcount
    state.foodsChoosed.forEach(food => (food.count = 0));
    //
    state.foodsChoosed = [];
  },
  //*
  [SEARCH_SHOP_LIST](state, { searchedShops }) {
    state.searchedShops = searchedShops;
  }
};



